<template>
  <div id="app">
      <div class="left">
        <div class="head-img">
          <img src="./assets/b.jpg" alt="">
          <p>cc管理员</p>
        </div>
        <div class="router" >
          <ul v-for="item in menu">
              <li><router-link :to='item.path'>{{item.name}}</router-link></li>
              <li v-for="child in item.child"><router-link :to='child.path'>{{child.name}}</router-link></li>
          </ul>
        </div>
      </div>
      <div class="right">
        <p class="header">管理后台</p>
        <transition name="fade" mode="out-in">
          <router-view  class="content"></router-view>
        </transition>
      </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return { 
     menu: [
        {name:"首页",path:"/"},
        {name:"管理",path:"/manage",child:[{name:"文章管理",path:"/manage/article"},{name:"用户管理",path:"/manage/user"}]},
        {name:"统计",path:"/statistics",child:[{name:"用户分析",path:"/statistics/user"}]}
      ]
    }
  }
}
</script>
<style scoped>
  /*只在本模板中起作用*/
</style>
<style lang="scss">
@import './assets/css/common.scss';

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
.child-view {
  position: absolute;
  transition: all .5s cubic-bezier(.55,0,.1,1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  width:100%;
  height:100%;

}
a{
  color:black;
  text-decoration: none;
}

*{background-color: rgba(0,0,0,0.1)}

ul {
  list-style-type: none;
  padding: 0;
  margin:0;
}

li {
   padding: 0;
   margin:0;
}

p{
  margin:0;
}
.left{
  width:$left-width;
  height: 100%;
  float: left;
  box-sizing: border-box;
  background-color: white;
  position: relative;
}
.right{
  height: 100%;
   background-color: #f6f6f6;
}
.router{
  margin:auto;
  margin-top: 100px;
  width:100px;

}
.router ul:not(:last-child){
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(0,0,0,0.3)
}
.content{
  /*margin:auto;*/
  margin-left: $left-width;
  padding:20px;
  box-sizing: border-box;
}
.header{
   height: $top-width;
   background-color: #1fcc8c;
   color:white;
   text-align: left;
   line-height: $top-width;
   padding-left: 40px;
   margin:0;
   margin-left: $left-width;
  
}
.head-img{
  width:100px;
  height: 100px;
  text-align: center;
  margin:auto;
  margin-top:30px;
  box-sizing: border-box;
}
.head-img img{
  width:50px;
  height: 50px;
  border-radius: 100%;
}
html,body{
  width:100%;
  height:100%;
  margin:0;
  padding:0;
}
</style>



